<template>
  <div>
    <v-container>
      <div v-if="accessToken">
        <div v-if="profile">
          <h1>PROFILE</h1>
          <br>
          <p><strong>NAME</strong></p>
          <p>{{ profile.first_name }} {{ profile.last_name }}</p>
          <br>
          <p><strong>EMAIL</strong></p>
          <p>{{ profile.email_id }}</p>
        </div>
        <div v-else>
          <h1>Sorry, your session has expired, please login again</h1>
        </div>
      </div>
      <div v-else>
        <h1>You need to login to see your dashboard</h1>
      </div>
    </v-container>
  </div>
</template>

<script>

  import axios from 'axios';
  const AxonApiURL = process.env.VUE_APP_AxonApiURL;

  export default {
    data: () => ({
        profile: null,
        accessToken: null
    }),

    created: async function() {
      this.accessToken = localStorage.getItem('AccessToken');

      if(this.accessToken){
        try{
          let response = await axios.get(`${AxonApiURL}/userGet?access_token=${this.accessToken}`);
          this.profile = response.data.profile;
        }
        catch(error){
          console.log(error);
          this.profile = null;
        }
      }
    },
  };
</script>

