<template>
	<div>
		<v-container>
			<h1>You're almost done!</h1>
			<br>
			<h2>We just need to verify your identity:</h2>
			<br>
			<v-btn
				color='blue'
				class='mr-4'
				@click='send_email'
			>E-Mail Me</v-btn>

			<v-btn
				color='blue'
				class='mr-4'
				@click='send_sms'
			>Text Me</v-btn>
		</v-container>
	</div>
</template>

<script>
	import axios from 'axios';

	const AxonApiURL = process.env.VUE_APP_AxonApiURL;

	export default {
		data: () => ({
			username: null
		}),

		methods: {
			async send_email(){
				//TODO: email the user a code
				//for now lets just send the email from Okta, then send them to login page.

				try{
					
					await axios.post(`${AxonApiURL}/userActivate`, {activation_method: 'email', user_name: this.username});
					this.$alert(`Activation E-mail sent to ${this.username}!`);
					this.$router.push('Login');
				}
				catch(error){
					console.log(error);
					this.$alert('Sorry, there was a problem activating your account');
					this.$router.push('/');
				}
			},

			async send_sms(){
				//TODO: send the user a text
			}
		},

		created: function(){
			this.username = JSON.parse(localStorage.getItem('user_name'));

			if (this.username){
				localStorage.removeItem('user_name');
			}
			else{
				this.$router.push('/');
			}
		}
	}

</script>