<template>
  <v-app>
    <v-app-bar app color="#41B883" dark>
      <div class="d-flex align-center">
        <router-link to="/" style="color: #fff; text-decoration: none"
          ><h1>Sample Consuming App</h1></router-link
        >
      </div>

      <div style="margin-left: 50px">
        <router-link to="/dashboard" style="color:#fff;text-decoration:none;">
          DASHBOARD
        </router-link>
      </div>

      <v-spacer></v-spacer>
      <router-link to="login">
        <v-btn color="blue"> LOGIN </v-btn></router-link
      >
      
      <v-btn color="black" style="margin-left:10px" @click="logout"> LOGOUT </v-btn>
    </v-app-bar>

    <v-main>
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>
  export default {
    name: "App",
    data: () => ({
      //
    }),
    methods: {
      logout: function (){
        localStorage.removeItem('AccessToken');

        if(window.location.pathname != '/'){
          this.$router.push("/");
        }
      }
    }
  };
</script>
