<template>
  <v-container>
    <div id="widget-container"></div>
    <br>
    <v-col class='text-center'>
      <h2>
        <router-link to='reset_password'>Forgot Password</router-link>
      </h2>
    </v-col>
  </v-container>
</template>

<script>
import OktaSignIn from '@okta/okta-signin-widget';
import '@okta/okta-signin-widget/dist/css/okta-sign-in.min.css';

const oktaInstanceURL = process.env.VUE_APP_oktaInstanceURL;
const oktaAuthServerURL = process.env.VUE_APP_oktaAuthServerURL;
const clientID = process.env.VUE_APP_testClientID;


export default {
  data: () => ({signIn: null}),
  mounted: async function () {
    let accessToken = localStorage.getItem("AccessToken");

    if (accessToken) {
      this.$router.push("Dashboard");
      return;
    }
        
    this.signIn = new OktaSignIn({
      baseUrl: oktaInstanceURL,
      el: '#widget-container',
      clientId: clientID,
      redirectUri: 'http://localhost:8080',
      //redirectUri: 'http://138.0.230.99:8080',
      authParams: {
        issuer: oktaAuthServerURL
      }
    });

    try{
      let tokens = await this.signIn.showSignInToGetTokens();
      localStorage.setItem("AccessToken", tokens.accessToken.value);
      this.$router.push("Dashboard");
    }
    catch(error){
      console.log(error);
    }
  },
  destroyed: function() {
    if(this.signIn){
      this.signIn.remove();
    }
  }
};
</script>

