<template>
	<v-container>
		<v-row>
			<h2>Enter the email you created your account with so we can send you a recovery e-mail:</h2>
		</v-row>

		<v-row>
			<v-col>
				<v-form ref="form" v-model="valid" lazy-validation>

					<v-text-field
						v-model="email"
						:rules="emailRules"
						label="E-mail"
						required
					></v-text-field>

					<v-btn
						:disabled="!valid"
						color="success"
						class="mr-4"
						@click="recover"
					>
						Go
					</v-btn>

				</v-form>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
	import axios from 'axios';

	const AxonApiURL = process.env.VUE_APP_AxonApiURL;

	export default {
		data: () => ({
			valid: true,
			email: "",

			emailRules: [
				(v) => !!v || 'E-mail is required',
				(v) => /.+@.+\..+/.test(v) || "E-mail must be valid"
			]
		}),

		methods: {
			async recover() {

				try{

					await axios.get(`${AxonApiURL}/userResetPswd?user_name=${this.email}`);

					this.$alert(`Recovery E-mail has been sent to ${this.email}!`);
					this.$router.push('login');
				}
				catch(error){
					console.log(error);
					this.$alert('Sorry, there is no account with that E-mail');
				}
			}
		},

		mounted: function() {
			this.$refs.form.validate();
		}
	};

</script>


