<template>
	<v-container>
		<v-row>
			<v-col>
				<v-form ref="form" v-model="valid" lazy-validation>

					<v-text-field
						v-model="first"
						:rules="fnameRules"
						label="First Name"
						required
					></v-text-field>

					<v-text-field
						v-model="last"
						:rules="lnameRules"
						label="Last Name"
						required
					></v-text-field>

					<v-text-field
						v-model="email"
						:rules="emailRules"
						label="E-mail"
						required
					></v-text-field>

					<!-- <v-text-field
						v-model="username"
						:rules="usernameRules"
						label="Username"
					></v-text-field> -->

					<v-text-field
						v-model="nickname"
						:rules="nicknameRules"
						label="Nickname"
					></v-text-field>


					<v-text-field
						v-model="phone"
						:rules="phoneRules"
						label="Phone Number"
					></v-text-field>

					<v-text-field
						v-model="AlconID"
						label="Alcon ID"
						:rules="AlconIDrules"
					></v-text-field>

					<!--
					<v-text-field
						v-model="password"
						type="password"
						:rules="pwRules"
						label="Password"
						required
					></v-text-field>

					<v-text-field
						v-model="confpwd"
						type="password"
						:rules="confpwRules"
						label="Confirm Password"
						required
					></v-text-field>
					-->

					<v-btn
            :disabled="!valid"
            color="success"
            class="mr-4"
            @click="register"
          >
            REGISTER
          </v-btn>
        </v-form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
	import axios from 'axios';

	const AxonApiURL = process.env.VUE_APP_AxonApiURL;

	export default {
		data: () => ({
			valid: true,
			first: "",
			last: "",
			email: "",
			phone: "",
			AlconID: "",
			username: "",
			nickname: "",
			//password: "",
			//confpwd: "",

			fnameRules: [(v) => !!v || 'First name is required'],
			lnameRules: [(v) => !!v || 'Last name is required'],
			
			emailRules: [
				(v) => !!v || 'E-mail is required',
				(v) => /.+@.+\..+/.test(v) || "E-mail must be valid"
			],

			phoneRules: [
				(v) => /^[0-9]{0,15}$|^$/.test(v) || 'Phone number must be 15 or fewer numeric characters' 
			],

			AlconIDrules: [(v) => /(^[0-9]{1,9}$)|^$/.test(v) || 'AlconID must be a number no more than 10 digits long'],

			usernameRules: [
				(v) => v.length <= 50 || 'Username must be 50 characters or less',
				(v) => /^[0-9A-Za-z_@.-]*$|^$/.test(v) || 'Valid username characters are limited to 0-9A-Za-z@_.-'
			],

			nicknameRules: [(v) => v.length <= 50 || 'Nickname must be 50 characters or less']

			// pwRules: [
			// 	(v) => !!v || 'Password is required',
			// 	(v) => /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/.test(v) || 
			// 		'Password must be at least 8 characters long, and contain at least one uppercase, one lowercase, and one number'
			// ],
			// confpwRules: [
			// 	(v) => !!v || 'You must confirm your password',
			// 	//(v) => !!v || 'Passwords must match'
			// ]
		}),

		methods: {
			async register() {
				// if (this.password !== this.confpwd){
				// 	this.$alert('Your passwords must match');
				// 	return;
				// }

				localStorage.removeItem('AccessToken');


				let userData = {
					first_name: this.first,
					last_name: this.last,
					email_id: this.email,
					user_name: this.username,
					nick_name: this.nickname,
					mobile_phone: this.phone,
					alcon_id: this.AlconID
				};

				try{
					await axios.post(`${AxonApiURL}/userCreate`, userData);
					let u_name = this.username ? this.username : this.email;
					localStorage.setItem('user_name', JSON.stringify(u_name));
					this.$router.push("Verify");
				}
				catch(error){
					console.log(error);
					this.$alert('Sorry, there was a problem creating your account');
					this.$router.push("/");
				}
			}
		},

		mounted: function() {
			this.$refs.form.validate();
		}
	};
</script>

